@import '../../css/variables';

.switch-icon {
  width: 32px;
  height: auto;
}

.switch {
  display: none;
}

.add-card-form {
  width: 90%;
  margin: 20px auto 0 auto;
  display: flex;
  flex-direction: column;
  gap: 20px;

  .form-item {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    label {
      flex: 1 1 30%;
    }

    input,
    select {
      flex: 1 1 70%;
    }

    select:hover {
      cursor: pointer;
    }

    .input-description {
      height: 60px;
      word-wrap: break-word;
      word-break: break-all;
    }
  }
}

.form-item-switch {
  display: flex;
  flex-direction: row;
  align-items: center;

  span {
    width: 30%;
  }

  img,
  span {
    cursor: pointer;
  }
}

.btn-submit {
  margin-top: 40px;
}

.form-item-checkbox {
  display: flex;
  flex-direction: row;
  align-items: center;

  div {
    flex: 1 1 30%;
  }
  .boxes {
    flex: 1 1 70%;
    background-color: $COLOR-PRIMARY;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: row;
    align-items: center;

    label {
      flex: 1 1 auto;
      cursor: pointer;

      input {
        margin: 0 10px;
        cursor: pointer;
      }
    }
  }

}

button,
input[type="file"]:hover {
  cursor: pointer;
}

.output {
  font-size: small;
}
