@import '../../css/variables';

$FONT-PRODUCT-NAME: 'Courier New', Courier, monospace;

.card-container {
  margin: 0;
  padding: 10px;
  border: 1px solid $COLOR-PRIMARY-DARK;
  width: $CARD-WIDTH;
  height: 450px;
  display: flex;
  flex-direction: column;
  box-shadow: 4px 4px 8px $COLOR-SECONDARY-DARK;
  border-radius: 5px;
  background-color: white;
  overflow: hidden;

  &:hover {
    box-shadow: 4px 4px 30px $COLOR-SECONDARY-DARK;
    transform: scale(1.025);
    cursor: pointer;
    background-color: $COLOR-PRIMARY-LIGHT;
  }

  .image-container {
    .preview-img {
      width: calc($CARD-WIDTH - 20px);
      height: calc($CARD-WIDTH - 20px);
      border-radius: 5px;
    }
  }

  .product-info {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding-bottom: 5px;

    .product-brand {
      font-weight: bold;
      text-transform: uppercase;
      color: $COLOR-SECONDARY-DARK;
    }

    .product-name {
      font-family: $FONT-PRODUCT-NAME;
      height: 2.5rem;
    }

    .product-description {
      font-size: small;
      border-top: 1px dotted lightgray;
      border-bottom: 1px dotted lightgray;
      height: 3.5rem;
    }

    .product-price {
      font-weight: bold;
      margin-top: auto;
      align-self: center;
    }
  }

  .product-features {
    .feat {
      font-size: small;
    }

    .feat-title {
      color: $COLOR-SECONDARY-DARK;
      padding: 0 5px;
    }
  }

  .product-actions {
    display: flex;
    flex-direction: row;
    width: $CARD-WIDTH;
    margin-left: -10px;
    margin-bottom: -10px;
    padding: 0 14px 0 0;
    align-items: center;
    background-color: lightgray;
    height: 2.5rem;

    .btn-fav {
      width: 40px;
      height: 1.5rem;
      border: none;
      background-color: transparent;

      &:hover {
        cursor: pointer;

        .btn-icon {
          height: 1.2rem;
        }
      }

      .btn-icon {
        height: 1rem;
        width: auto;
        align-self: center;
      }
    }

    .btn-addToCart {
      margin-left: auto;
      display: flex;
      width: 40px;
      height: 1.5rem;
      padding: 3px 5px;
      justify-content: center;

      &:hover {
        cursor: pointer;
      }

      .btn-icon {
        height: 1rem;
        width: auto;
        align-self: center;
      }
    }
  }
}
