@import '../../css/variables';

.footer-container {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 10px 20px;

  background-color: $COLOR-SECONDARY;
  color: $COLOR-PRIMARY-DARK;

  @media only screen and (max-width: 320px) {
    flex-direction: column;
    align-items: center;
  }
}

.footer-github-link {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: $COLOR-PRIMARY-DARK;

  &:visited {
    color: $COLOR-PRIMARY-DARK;
  }

  span {
    padding-left: 8px;
  }
}

.footer-rslogo {
  width: 50px;
  height: 2rem;
}

.github-logo {
  height: 1.5rem;
  width: 1.5rem;
}
